<template>
  <el-card style="text-align: left;width: 600px;margin: 35px auto 0 auto">
    <div v-if="test" class="test-configuration">
        <h1 style="text-align: center">测试配置</h1>
          <div><span style="font-size: 20px">测试时长:{{ test.duration}}</span></div>
          <div><span style="font-size: 20px">测试对象:{{ test.system.type}}</span></div>
          <div><span style="font-size: 20px">变异策略:</span></div>
          <div>环境要素</div>
          <div><li style="font-size: 18px">{{ test.environments.type}}</li></div>
          <div>静态障碍物</div>
          <div><li style="font-size: 18px">行人:{{ test.pedestrian.type}}</li></div>
          <div><li style="font-size: 18px">路障:{{ test.roadblock.type}}</li></div>
          <div>动态障碍物</div>
          <div><li style="font-size: 18px">机动车:{{ test.motorVehicles.type}}</li></div>
          <div><li style="font-size: 18px">自行车:{{ test.bicycles.type}}</li></div>
          <div>EGO</div>
          <div><li style="font-size: 18px">{{ test.ego.type}}</li></div>
          <div>交通设施</div>
          <div><li style="font-size: 18px">{{ test.trafficFacilities.type}}</li></div>
          <div><span style="font-size: 20px">场景评估策略:{{ test.evaluationStrategies.type}}</span></div>
          <div><span style="font-size: 20px">初始场景:{{ test.initialScenario.type}}</span></div>
          <div><span style="font-size: 20px">目标事故类型:{{ test.targetAccidents.type}}</span></div>
    </div>
    <div class="test-result">
        <h1 style="text-align: center">测试结果</h1>
          <div><span style="font-size: 20px">测试的场景总数:{{test.totalTestedScenarios}}个</span></div>
          <div><span style="font-size: 20px">事故场景总数:{{test.collisionAccidents + test.offRoadAccidents}}个</span></div>
          <router-link class="to-accident-list" :to="{path:'/accidentIndex', query:{id: test.id}}">
          <ul>
            <div><li style="font-size: 18px">碰撞:{{test.collisionAccidents}}个</li></div>
            <div><li style="font-size: 18px">驶出道路:{{test.offRoadAccidents}}个</li></div>
          </ul>
          </router-link>
    </div>
  </el-card>
</template>

<script>

export default {
  name: 'TestResult',
  data () {
    return {
      test: ''
    }
  },
  mounted () {
    this.loadTest()
  },
  methods: {
    loadTest () {
      var _this = this
      this.$axios.get('/testcase/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.test = resp.data.result
          // console.log(_this.test)
        }
        let cur = _this.test
        if (cur.pedestrian.type.includes('增减')) {
          cur.pedestrian.type = '(' + cur.minPedestrian + '-' + cur.maxPedestrian + ')' + cur.pedestrian.type
        }
        if (cur.roadblock.type.includes('增减')) {
          cur.roadblock.type = '(' + cur.minRoadblock + '-' + cur.maxRoadblock + ')' + cur.roadblock.type
        }
        if (cur.motorVehicles.type.includes('增减')) {
          cur.motorVehicles.type = '(' + cur.minMotorVehicles + '-' + cur.maxMotorVehicles + ')' + cur.motorVehicles.type
        }
        if (cur.bicycles.type.includes('增减')) {
          cur.bicycles.type = '(' + cur.minBicycles + '-' + cur.maxBicycles + ')' + cur.bicycles.type
        }
      })
    }
  }
}
</script>

<style scoped>
  .to-accident-list {
    text-decoration: none;
   color: #606266;
  }
  .to-accident-list:hover {
    color: #409EFF;
  }
  @import "../../styles/markdown.css";
</style>



